import React from 'react'
import Layout from '../components/Layout/Layout'
import web from '../images/eabout.jpg'
import Comman from "../components/Common"
const AboutPage = () => {
  return (
    <Layout tittle={"About us - Ecommerce App"}>
  
      <Comman name="WELCOME TO ABOUT PAGE"
      imgsrc={web}
      visit="/contact"
      btnname="Contact Now" />
    
    </Layout>
  ) 
}

Layout.defaultProps = {
  tittle: 'Ecommerce-App - shop now',
  description:'MERN-stack Project', 
  keywords:'node, react, express, mongodb', 
  author:'Nishant'
}
export default AboutPage