import React,{useEffect, useState} from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import toast from "react-hot-toast";
import axios from "axios";
import {Select} from "antd";
import { useNavigate, useParams } from 'react-router-dom';
const {Option} = Select;


const UpdateProduct = () => {

    const navigate = useNavigate();
    const params = useParams();
    const [categories, setCategories] = useState([])
    const [photo, setPhoto] = useState("")
    const [name, setName] = useState("")
    const [description, setDescription] = useState("")
    const [price, setPrice] = useState("")
    const [category, setCategory] = useState("")
    const [quantity, setQuantity] = useState("")
    const [shipping, setShipping] = useState("")
    const [id, setId] = useState('')
    //Get  Single Product

    const getSingleProduct = async()=> {
        try {
            const {data} = await axios.get(`/api/v1/product/single-product/${params.slug}`);
            console.log(data);
            setName(data.product.name);
            setId(data.product._id);
            setDescription(data.product.description);
            setPrice(data.product.price);
            setCategory(data.product.category._id);
            setQuantity(data.product.quantity);
            setPhoto(data.product.photo);
            setShipping(data.product.shipping);
        } catch (error) {
            console.log(error);
        }
    };
    useEffect(()=>{
        getSingleProduct(); 
        //eslint-disable-next-line
    },[]);


    // Get All Categories
    const getAllCategory = async () => {
      try {
        const { data } = await axios.get("/api/v1/category/all-category");
        if (data?.success) {
          setCategories(data?.category);
        }
      } catch (error) {
        console.log(error);
        toast.error("something went wrong while getting category.");
      }
    };
  
    useEffect(() => {
      getAllCategory();
    }, []);
  
  // Create Product handleupdate
  const handleUpdate =async(e)=>{
    e.preventDefault();
    try {
      const productData =new FormData()
      productData.append("name",name)
      productData.append("description",description)
      productData.append("price",price)
      productData.append("quantity",quantity)
      photo && productData.append("photo",photo)
      productData.append("category",category)
      // productData.append("shipping",shipping)
      const {data} =await axios.put(`/api/v1/product/update-product/${id}`,
      productData);
      if(data?.success){
        toast.success('Product Updated Successfully');
        navigate("/dashboard/admin/products");
      }else{
        toast.error(data?.message)
      }
    } catch (error) {
      console.log(error)
      toast.error('Something  went wrong');
    }
  }

  // Create ProductDelete
  const handleDelete = async()=>{
    try {
        let answer = window.prompt('Are you sure  want to delete this product');
        if(!answer) return;
        const {data} = await axios.delete(
            `/api/v1/product/delete-product/${id}`
            );
        toast.success('Product is deleted');
        navigate('/dashboard/admin/products');
    } catch (error) {
        console.log(error);
        toast.error('something went wrong');
    }
  };
  return (
    <Layout tittle={'Create Product - Ecommerce App'}>
    <div className='container-fluid'>
    <div className='row p-3'>
        <div className='col-md-3'>
            <AdminMenu/>
        </div>
        <div className='col-md-9'>
        <h1>Update Product</h1>
        <div className='m-1 w-75'>
          <Select bordered={false}
          placeholder="Selecct a Category"
          size='large'
          showSearch 
          className='form-select mb-3'
          onChange={(value) => {setCategory(value)}}
          value={category}
          >
            
          {categories?.map(c =>(
            <Option key={c._id} value={c._id}>
              {c.name}
              </Option>
          ))}
          </Select>
          <div className='mb-3'> 
            <label className='btn btn-outline-success col-md-12'>
            {photo ? photo.name : "Upload Photo"}
            <input 
            type='file'
            name='' 
            accept='image/*' 
            onChange={(e)=>setPhoto(e.target.files[0] )} 
            hidden/>
            </label>
          </div>
          <div className='mb-3'>
            {photo  ? (
              <div className='text-center'>
                <img src={URL.createObjectURL(photo)} 
                alt='product_photo' 
                height={'200px'} 
                className='img img-responsive'/>
              </div>
            ): (
                <div className='text-center'>
                <img src={`/api/v1/product/product-photo/${id}`} 
                alt='product_photo' 
                height={'200px'} 
                className='img img-responsive'/>
              </div>
            )}
          </div>
          <div className='mb-3'>
            <input 
            type='text' 
            value={name} 
            placeholder='Write a Name' 
            className='form-control' 
            onChange={(e)=>setName(e.target.value)}
            />
          </div>
          <div className='mb-3'>
            <textarea 
            type='texarea' 
            value={description} 
            placeholder='Write a description' 
            className='form-control' 
            onChange={(e)=>setDescription(e.target.value)}
            ></textarea>
          </div>
          <div className='mb-3'>
            <input 
            type='number' 
            value={price} 
            placeholder='Enter a Price Value' 
            className='form-control' 
            onChange={(e)=>setPrice(e.target.value)}
            />
          </div>
          <div className='mb-3'>
            <input 
            type='number' 
            value={quantity} 
            placeholder='Enter Quantity' 
            className='form-control' 
            onChange={(e)=>setQuantity(e.target.value)}
            />
          </div>
          <div className='mb-3'>
            <Select
            bordered={false} 
            size='large' 
            showSearch 
            placeholder='Select Shipping' 
            className='form-control' 
            onChange={(value)=>{
              setShipping(value);
            }}
            value={shipping ? "Yes" : "No"}
            >
              <Option value="0">No</Option>
              <Option value="1">Yes</Option>
            </Select>
          </div>
          <div className='mb-3'>
            <button className='btn btn-outline-primary' onClick={handleUpdate}>UPDATE PRODUCT</button>
          </div>
          <div className='mb-3'>
            <button className='btn btn-outline-danger' onClick={handleDelete}>DELETE PRODUCT</button>
          </div>
        </div>
        </div>
    </div>
    </div>
    </Layout>
  )
}

export default UpdateProduct