import React from 'react'
import Layout from '../components/Layout/Layout'
import web from '../images/about.png'
const PolicyPage = () => {
  return (
    <Layout tittle={"Privacy Policy - Ecommerce App"}>
        <section id="header" className="d-flex align-items-center">
        <div className="container-fluid">
          <div className="row">
            <h3 className='text-center'>PRIVATE POLICY</h3>
            <div className="col-10 mx-auto">
              <div className="row ">
              <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                <img src={web} alt='about page'/>
              </div>
              <div className="col-md-6 pt-5 pt-lg-0 order-1 order-lg-2 d-flex justify-content-center flex-column">
                <ul>
                  <li className='m-3'>Dummy Text</li>
                  <li className='m-3'>Dummy Text</li>
                  <li className='m-3'>Dummy Text</li>
                  <li className='m-3'>Dummy Text</li>
                  <li className='m-3'>Dummy Text</li>
                </ul>
              </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default PolicyPage