import React from 'react'
import Layout from '../../components/Layout/Layout'
import UserMenu from '../../components/Layout/UserMenu'
import { useAuth } from '../../context/auth'
const Dashboard = () => {
  const [auth] = useAuth();
  return (
    <Layout tittle={"Dashboard - Ecommmerce App"}>
        
    <div className='container-fluid'>
    <div className='row p-3'>
      <div className='col-md-3'>
        <UserMenu/>
      </div>
      <div className='col-md-9'>
        <div className='card w-75 p-2 '>
          <h5>User Name:     {auth?.user?.name}</h5>
          <h6>User Email id: {auth?.user?.email}</h6>
          <h6>User Phone No: {auth?.user?.phone}</h6>
          <h6>User Address:  {auth?.user?.address}</h6>
        </div>

      </div>
    </div>
    </div>
    </Layout>
  
  )
}

export default Dashboard