 import React,{useState, useEffect} from 'react'
import Layout from '../components/Layout/Layout'
import axios from 'axios'
import {Checkbox, Radio} from 'antd'
import { Prices } from '../components/Prices'
import cors from 'cors'
import { useNavigate } from 'react-router-dom'
import {useCart} from '../context/cart';
import toast from 'react-hot-toast'
import web from '../images/ebanner.jpg'

const HomePage = () => {
  const navigate = useNavigate();
  const [cart, setCart] = useCart();
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setradio] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
// Get All Categories
const getAllCategory = async () => {
  try {
    const { data } = await axios.get("/api/v1/category/all-category");
    if (data?.success) {
      setCategories(data?.category);
    }
  } catch (error) {
    console.log(error);
  }
};

useEffect(() => {
  getAllCategory();
  getTotal();
}, []);




//Get All Product

  const getAllProduct = async()=>{
    try {
      setLoading(true);
      const {data} = await axios.get(`/api/v1/product/product-list/${page}`)
      setLoading(false)
      setProducts(data?.products);
    } catch (error) {
        setLoading(false)
        console.log(error);
    }

  }


// Get Total Count.
const getTotal = async()=>{
  try {
    const  {data} = await axios.get('/api/v1/product/product-count')
    setTotal(data?.total)
  } catch (error) {
    console.log(error);
  }
}

useEffect(()=>{
  if(page === 1) return;
  loadMore();
},[page]);
//Load More
const loadMore = async()=>{
  try {
    const {data} = await axios.get(`/api/v1/product/product-list/${page}`)
    setLoading(false)
    setProducts([...products, ...data?.products])
  } catch (error) {
    setLoading(false)
    console.log(error);
  }
}

// Fillter By Category
const handleFilter = (value,  id)=>{
  let all = [...checked]
  if(value){
    all.push(id)
  }else{
    all = all.filter((c) => c !== id);
  }
  setChecked(all);
};
  useEffect(()=>{
    if(!checked.length || !radio.length) getAllProduct();
  },[checked.length, radio.length])

  useEffect(()=>{
    if(checked.length || radio.length) filterProduct();
  },[checked, radio]);

//Get Filttered Product 
  const filterProduct = async()=>{
    try {
      const {data} = await axios.post('/api/v1/product/product-filters',{checked, radio})
      setProducts(data?.products)      
    } catch (error) {
      console.log(error)
    }
  }
  return (
    <Layout tittle={"Shop Now - Ecommerce App"}>
      <div className='banner'>
        <div className='col-12 py-2'>
        <img src={web} className="img-fluid" alt="home-img" />
        </div>
      </div>
      <div className='row mt-3 m-0'>
        <div className='col-md-3 mb-5'>
          <h5 className='text-center mb-5'>FILLTER BY CATEGORY</h5>
          <div className='d-flex flex-column ms-3'>
          {categories?.map((c)=>(
            <Checkbox key={c._id} onChange={(e)=>handleFilter(e.target.checked,c._id)}>{c.name}</Checkbox>
          ))}
          </div>

          {/* Price filter */}
          <h5 className='text-center mt-5 mb-3'>FILTER BY PRICE</h5>
          <div className='d-flex flex-column ms-3'>
          <Radio.Group onChange={e => setradio(e.target.value)}>
            {Prices?.map(p =>(
              <div  key={p._id}>
              <Radio value={p.array}>{p.name}</Radio>
              </div>
            ))}
          </Radio.Group>
          </div>

          <div className='d-flex flex-column ms-3'>
          <button className='btn btn-outline-secondary m-5' onClick={()=>window.location.reload()}>RESET FILTER</button>
          </div>
        </div>
        <div className='col-md-9 text-center'>
          <h3 className='text-center mb-3'>ALL PRODUCTS</h3>
          <div className='d-flex flex-wrap'>
            {products?.map(p => (
            
            <div className="card m-2" style={{ width: "18rem" }} >
              <img src={`/api/v1/product/product-photo/${p._id}`} className="card-img-top" alt={p.name} />
              <div className="card-body">
                <div className='d-flex '>
                <h5>$ {p.price}</h5>
                <h5 className="card-title ms-auto">{p.name}</h5>
                </div>
                <p className="card-text">
                  {p.description.substring(0, 30)}
                </p>
                <button  class="btn btn-primary" onClick={()=> navigate(`/product/${p.slug}`)}>More Details</button>
                <button  class="btn btn-secondary ms-2" 
                onClick={()=>{setCart([...cart,p])
                  localStorage.setItem('cart',JSON.stringify([...cart,p]))
                toast.success('Item Added to Cart');
                }}>ADD TO CART</button>
               </div>
            </div>
            
          ))}
          </div>
          <div className='m-2 p-3'>
            {products && products.length < total &&(
            <button className='btn btn-warning' onClick={(e)=>{
              e.preventDefault();
              setPage(page + 1);
            }}>
              {loading ? "Loading...": "Load More"}
            </button>)}
            
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default HomePage