import React from 'react'
import Layout from '../components/Layout/Layout'
import web from '../images/about.png'
import Comman from "../components/Common"


const ContactPage = () => {
  return (
    <Layout tittle={"Contact us - Ecommerce App"}>
    <Comman name="WELCOME TO CONTACT PAGE"
      imgsrc={web}
      visit="/contectform"
      btnname="Contact Now" />
    </Layout>
  )
}

export default ContactPage