import React,{useState, useEffect} from 'react'
import Layout from '../components/Layout/Layout'
import axios from 'axios'
import { useParams } from 'react-router-dom'
const ProductDetail = () => {
const params = useParams();
const [product, setProduct] = useState({});
// Initial Product Details
useEffect(()=>{
   if(params?.slug) getProduct();
},[params?.slug])

//Get Product
const getProduct = async()=>{
    try {
        const {data} =  await axios.get(`/api/v1/product//single-product/${params.slug}`)
        setProduct(data?.product)
    } catch (error) {
        console.log(error);
    }
}

  return (
    <Layout>
        
        <div className='row m-0 p-5'>
            <div className='col-md-5'>
            <img src={`/api/v1/product/product-photo/${product._id}`} 
            className="card-img-top" 
            alt={product.name}
            height={500}
            />
            </div>
            <div className='col-md-5 '>
                <h3 className='text-center mb-5'>PRODUT DETAILS</h3>
                <h6>Name: {product?.name}</h6>
                <h6>Description: {product?.description}</h6>
                <h6>Price: $ {product.price}</h6>
                {/* <h6>Category: {product?.category.name}</h6> */}
                <button  class="btn btn-secondary mt-5">ADD TO CART</button>
            </div>
        </div>
    </Layout>
  )
}

export default ProductDetail