import Layout from '../components/Layout/Layout'
import React, {useState} from 'react'

const ContectForm = () => {
    const[data, setData] = useState({
        fullname:"",
        phone:"",
        email:"",
        msg:"",
      })
    
      const inputEvent = (e) => {
        const {name, value} = e.target;
        
        setData((preVal)=>{
          return({
            ...preVal,
            [name]: value,
          })
        })
      }
    
      const formSubmit = (e) =>  {
        e.preventDefault();
        alert(`My Name is ${data.fullname} my mobile no. is ${data.phone} and email id is ${data.email} and  i want to say ${data.msg}`);
       
            
       }



  return (
    <Layout>

<div className="my-5">
        <h1 className="text-center">CONTACT US <span id="name"></span></h1>
      </div>
      
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto form">
            <form action="" className="" onSubmit={formSubmit}>
              <div class="mb-3">
                <label for="exampleFormControlInput1" class="form-label">Name</label>
                <input 
                type="text" 
                class="form-control" 
                id="exampleFormControlInput1" 
                name="fullname"
                value={data.fullname}
                onChange={inputEvent}
                placeholder="Enter Your Full Name" />
              </div>
              <div class="mb-3">
                <label for="exampleFormControlInput1" class="form-label">Mobile Number</label>
                <input 
                type="number" 
                class="form-control" 
                id="exampleFormControlInput1" 
                name="phone"
                value={data.phone}
                onChange={inputEvent}
                
                placeholder="Enter Your mobile number" />
              </div>
              <div class="mb-3">
                <label for="exampleFormControlInput1" class="form-label">Email address</label>
                <input 
                type="email" 
                class="form-control" 
                id="exampleFormControlInput1" 
                name="email"
                value={data.email}
                onChange={inputEvent}
                
                placeholder="Enter Your Email Id" />
              </div>
              <div class="mb-3">
                <label for="exampleFormControlTextarea1" class="form-label">Message</label>
                <textarea 
                class="form-control" 
                id="exampleFormControlTextarea1"
                name="msg"
                value={data.msg}
                onChange={inputEvent} 
                rows="3"></textarea>
              </div>
              <div className="col-12">
                <button  type="submit" className="btn btn-outline-primary">Submit</button>
              </div>
            </form>
          </div>


        </div>
      </div>


    </Layout>
  )
}

export default ContectForm