import React from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import { useAuth } from '../../context/auth'
const AdminDashboard = () => {
  const [auth] = useAuth();

  return (
    <Layout tittle={'Admin dashboard - Ecommerce App'}>
    <div className='container-fluid'>
    <div className='row p-3'>
      <div className='col-md-3'>
        <AdminMenu/>
      </div>
      <div className='col-md-9'>
        <div className='card w-75 p-2 '>
          <h5>Admin Name:     {auth?.user?.name}</h5>
          <h6>Admin Email id: {auth?.user?.email}</h6>
          <h6>Admin Phone No: {auth?.user?.phone}</h6>
          <h6>Admin Address:  {auth?.user?.address}</h6>
        </div>

      </div>
    </div>
    </div>
    </Layout>
  )
}

export default AdminDashboard