import { useState } from 'react';
import React from 'react'
import Layout from './../../components/Layout/Layout';
import {toast} from 'react-hot-toast';
import axios from 'axios';
import {useNavigate,useLocation} from 'react-router-dom'
import { useAuth } from '../../context/auth';
const Login = () => {

  const [email, setEmail]= useState("");
  const [password, setPassword]= useState("");
  const [auth, setAuth]= useAuth();
  const Navigate = useNavigate();
  const location =useLocation();
  // Submit form function
  const handleSubmit = async(e)=>{
    e.preventDefault();
    try{
      const res = await axios.post('/api/v1/auth/login', {email, password});
      
      if(res.data.success){
        toast.success(res.data.message);
        setAuth({
          ...auth,
          user:res.data.user,
          token:res.data.token,
        })
        localStorage.setItem('auth', JSON.stringify(res.data));
        Navigate(location.state  || "/");
      }else{
        toast.error(res.data.message);
      }
    }catch(error){ 
      console.log(error);
      toast.error("Something gone Wrong");
    }
  }



  return (
    <Layout tittle= "Login Page - Ecommerce App">

<section className="vh-100" style={{backgroundColor: '#eee'}}>
  <div className="container h-100">
    <div className="row d-flex justify-content-center align-items-center h-100">
      <div className="col-lg-12 col-xl-11">
        <div className="card text-black" style={{borderRadius: 25}}>
          <div className="card-body p-md-1">
            <div className="row justify-content-center">
              <div className="col-md-10 col-lg-6 col-xl-5 order-2 order-lg-1">
                <p className="text-center h1 fw-bold mb-3 mx- mx-md-4 mt-3">LOGIN</p>
                <form className="mx-1 mx-md-4 " onSubmit={handleSubmit}>
                  
                  <div className="d-flex flex-row align-items-center mb-1">
                    <div className="form-outline flex-fill mb-2">
                      <label className="form-label" htmlFor="form3Example3c">Your Email</label>
                      <input type="email"
                      value={email}
                      onChange={(e)=> setEmail(e.target.value)}
                      className="form-control"  
                      aria-describedby="emailHelp" 
                      placeholder='Enter Your Email ID'
                      required/>
                     
                    </div>
                  </div>
                  <div className="d-flex flex-row align-items-center mb-1">                    
                    <div className="form-outline flex-fill mb-2">
                      <label className="form-label" htmlFor="form3Example4c">Password</label>
                      <input type="password"
                      value={password}
                      onChange={(e)=> setPassword(e.target.value)} 
                      className="form-control" 
                      placeholder='Enter Your Password'
                      required/>
                     
                    </div>
                  </div>

                  <div className="d-flex justify-content-center m-5 mb-3 mb-lg-4">
                    <button type="submit" className="btn btn-outline-secondary btn-lg me-5">Login</button>
                    <button type="button" className="btn btn-outline-secondary ms-5" onClick={()=>{Navigate('/forgot-Password')}}>Forgot Password </button>
                  </div>
                </form>
              </div>
              <div className="col-md-10 col-lg-6 col-xl-7 d-flex align-items-center order-1 order-lg-2">
                <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-registration/draw1.webp" className="img-fluid" alt="Sample image" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>



        {/* <div className='register'>
          <h1>Login Form</h1>
          <form onSubmit={handleSubmit}>
  
     <div className="mb-3">
    <input type="email"
     value={email}
     onChange={(e)=> setEmail(e.target.value)}
     className="form-control"  
     aria-describedby="emailHelp" 
     placeholder='Enter Your Email ID'
     required/>
     </div>
      <div className="mb-3">
        <input type="password"
        value={password}
        onChange={(e)=> setPassword(e.target.value)} 
        className="form-control" 
        placeholder='Enter Your Password'
        required/>
      </div>
      <button type="button" className="btn btn-primary me-3" onClick={()=>{Navigate('/forgot-Password')}}>Forgot setPassword </button>
<button type="submit" className="btn btn-primary">LOGIN</button>
</form>

        </div> */}

    </Layout>
  )
}

export default Login