import Layout from '../components/Layout/Layout'
import React from 'react'
import { useCart } from '../context/cart'
import { useAuth } from '../context/auth'
import { useNavigate } from 'react-router-dom'
const CartPage = () => {
  const [cart, setCart] = useCart();
  const [auth, setAuth] = useAuth();
  const navigate = useNavigate();

//Total Price
const totalPrice = ()=>{
  try {
    let total = 0;
    cart?.map((item) => {
      total += item.price;
    });
    return total;
  } catch (error) {
    console.log(error)
  }
}

//Remove Item from Cart
const  removeItem = (pid)=>{
  try {
    let myCart = [...cart];
    let index = myCart.findIndex(item => item._id === pid)
    myCart.splice(index, 1)
    setCart(myCart);
    localStorage.setItem('cart', JSON.stringify(myCart));
  } catch (error) {
    console.log(error)
  }
}


  return (
    <Layout>
        <div className='container'>
        <div className='row'>
        <div  className='col-md-12'>
        <h3 className='text-center bg-light p-2 mb-2 head'>
          {`HELLO ${auth?.token && auth?.user.name}`}
          </h3>
          <h5 className='text-center mt-3'>
            {cart && cart.length > 0 
            ? `YOU HAVE  ${cart.length}  ITEM IN YOUR CART ${
              auth?.token ? "" : "PLEASE LOGIN TO CHECKOUT"
              }`
               : "YOUR CART IS EMPTY"}
          </h5>
        
        </div>          
        </div>
        <div className='row'>
          <div className='col-md-8'>
            {
              cart?.map( p => (
                <div className='row card flex-row m-2 p-3'>
                  <div  className='col-md-4'>
                  <img src={`/api/v1/product/product-photo/${p._id}`} 
                  className="card-img-top mt-1 p-3" 
                  alt={p.name}
                  width={100}
                  height={100} />
                  </div>
                  <div  className='col-md-8 '>
                    <div className='p-5 m-3'>
                  <h6>NAME: {p.name}</h6>
                  <h6>DESCRIPTION: {p.description}</h6>
                  <h6>PRICE: ${p.price}</h6>
                  <button className='btn btn-outline-danger mt-5' 
                  onClick={()=> removeItem(p._id)}>REMOVE</button>
                  </div>
                  </div>
                </div>
              ))
            }
          </div>
          <div className='col-md-4 text-center'>
          <h3>CART SUMMARY</h3>
            Total || Checkout || Payment
            <hr/>
            <h4>Total : ${totalPrice()}</h4>
          </div>
        </div>
        </div>
    </Layout>
  )
}

export default CartPage 